<template>
  <div id="app">
    <el-button type="success">提交</el-button>
    <el-button :loading="isLoading" icon="el-icon-edit" type="primary" @click="click"></el-button>
    <el-button :disabled="disabled" type="info">提交</el-button>
    <el-button :disabled="disabled" type="warning">提交</el-button>
    <el-button :disabled="disabled" type="danger">提交</el-button>
  </div>
</template>

<script>
import ElButton from '@/components/ElButton'
export default {
  name: 'App',
  data () {
    return {
      disabled: false,
      isLoading: false
    }
  },
  mounted:function() {
    setTimeout(() => {
      this.disabled = true
    }, 2000);
  },
  components: {
    'el-button': ElButton
  },
  methods: {
    click () {
      this.isLoading = true;
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



