<template>
  <button class="el-button" :class= "[type?'el-button--' + type : '',
  {'is-disabled':buttonDisabled,'is-loading':isloading}
  ]" :disabled="buttonDisabled">
    <i class="el-icon-loading" v-if="isloading"></i>
    <i v-if="icon&&!isloading" :class="icon"></i>
    <slot></slot>
  </button>
</template>

<script>
export default {
  name: 'ElButton',
  props: {
    disabled: Boolean,
    type: {
      type: String,
      default: 'default'
    },
    isloading: Boolean,
    icon: {
      type: String,
      default: ''
    }
  },
  computed: {
    buttonDisabled () {
      return this.disabled;
    }
  }
}
</script>

<style>

</style>



